import React, { useContext } from 'react';
import Login from './login.jsx';
import Pass from './pass.jsx';
import { Context } from './page.jsx';

const Content = () => {

  const LoginContext = useContext(Context);
  console.log("LoginContext", LoginContext.state);

  let pageShow = <Login />;
  if (LoginContext.state.page === 'login') {
    pageShow = <Login />
  } else if (LoginContext.state.page === 'pass') {
    pageShow = <Pass />
  }
  
  return (
    <React.Fragment>
      {pageShow}
    </React.Fragment>
  )
}

export default Content;