import React, { useContext } from 'react';
import { Context } from '../containers/page.jsx';

const transfer = (date) => {
  date = date < 10 ? ('0' + date) : date;
  return date;
}

const EntryText = () => {

  const LoginContext = useContext(Context);
  const name = LoginContext.state.name;
  const grade = ["研究生（硕士）", "博士生（博士）"];
  const gradeName = grade[LoginContext.state.grade];

  const myDate = new Date();
  let [year, month, day] = [myDate.getFullYear(), myDate.getMonth() + 1, myDate.getDate()];
  let [hour, minute, second] = [myDate.getHours(), myDate.getMinutes(), myDate.getSeconds()];
  month = transfer(month);
  day = transfer(day);
  minute = transfer(minute);
  second = transfer(second);
  const currentTime = year + '-' + month + '-' + day + ' ' + hour + ':' + minute + ':' + second + " ";
  
  return (
    <React.Fragment>
      <div className="text-user-name">
        {name}
      </div>
      <div className="text-assign-type">
        {gradeName}
      </div>
      <div className="text-assign-time">
        <div className="moving-text">
          {currentTime}
        </div>
      </div>
      <div className="text-assign-time2">
        <div className="moving-text2">
          {currentTime}
        </div>
      </div>
    </React.Fragment>
  )
}

export default EntryText;