export const defaultState = {
  page: 'login',
  name: null,
  grade: null,
}

export function reducer(state, action) {
  switch(action.type) {
    case 'CONFIRM':
      return {
        page: action.page,
        name: action.name,
        grade: action.grade
      };
    case 'UNCONFIRM':
      return {
        page: action.page,
        name: action.name,
        grade: action.grade
      }
    default: 
      return state;
  }
}